    export default {
        props: {
            categories: {
                type: Array,
                default: [{
                        cateid: 0,
                        name: "待付款", 
                    },
                    {
                        cateid: 1,
                        name: "待发货"
                    },
                    {
                        cateid: 2,
                        name: "待收货"
                    },
                    {
                        cateid: 3,
                        name: "已完成"
                    },
                    {
                        cateid: 4,
                        name: "已关闭"
                    }
                ],
            },
            index:{
                type:Number,
                default:0
            },
			fontStyle: {
				type: String,
				default: ''
			}
        },
        watch:{
            index(){
                this.cateCurrentIndex=this.index; console.log(this.index,'this.index')
            }
        },
        mounted(){
            this.cateCurrentIndex=this.index;
        },
        computed: {
            getWidth() {
                return 'width:' + Math.floor(1 / this.categories.length * 100) + '%;';
            }
        },
        data() {
            return {
                // 当前选择的分类
                cateCurrentIndex: 0,
                // 内容实例，内容区域您根据项目情况设计即可，覆盖模式
                content: "全部"
            };
        },
        methods: {
            gotodemo: function() {
                uni.navigateTo({
                    url: '../segmented-control-demo/segmented-control-demo'
                })
            },
            tabChange: function(e) {
                // 选中的索引
                var index = e.currentTarget.dataset.index;
                // 具体的分类id
                var cateid = e.currentTarget.dataset.cateid;
                this.cateCurrentIndex = index;
                // 动态替换内容
                this.$emit('tabChange', {
                    cateid,
                    index,
                    name: this.categories[index].name,
                    searchId:this.categories[index].searchId,
                })
            }
        }
    }
